import countries from '../templates/countries.hbs';
import oneConutry from '../templates/oneConutry.hbs';
//pnotify
import { error } from '@pnotify/core';
import '@pnotify/core/dist/PNotify.css';
import '@pnotify/core/dist/BrightTheme.css';
import '@pnotify/confirm/dist/PNotifyConfirm.css';
//
import refs from './refs.js';

require('lodash');

const debounce = _.debounce(inputValue, 500);

refs.input.addEventListener('input', debounce);

function inputValue() {
  searchCountry(refs.input.value);
}

function searchCountry(searchQuery) {
  searchQuery = refs.input.value;
  if (searchQuery !== '') {
    fetch(`https://restcountries.eu/rest/v2/name/${searchQuery}`)
      .then(response => response.json())
      .then(data => {
        if (data.length === 1) {
          const markup = oneConutry(data);
          console.log(markup);
          refs.ul.innerHTML = markup;
        } else if (data.length <= 10) {
          const markup = countries(data);
          console.log(markup);
          refs.ul.innerHTML = markup;
        } else if (data.length > 10) {
          error({
            title: 'Too much coutries finded',
            width: '500px',
          });
          refs.ul.innerHTML = '';
        } else {
          error({
            title: 'Country not found',
            width: '500px',
          });
          refs.ul.innerHTML = '';
        }
      });
  } else {
    refs.ul.innerHTML = '';
  }
}
